.wrapper {
  background-color: #380B41;
}
canvas, .c2 {
  width: 100%;
  height: 100%;
  margin: 0;
  background: -webkit-gradient(linear, left top, left bottom, color-stop(70%, #380B41),to(#515175));
  background: -webkit-linear-gradient(top, #380B41 70%,#515175 100%);
  background: -o-linear-gradient(top, #380B41 70%,#515175 100%);
  background: linear-gradient(to bottom, #380B41 70%,#515175 100%);
  background-image: url(./prince.gif);
  background-position: center;
  background-repeat: no-repeat;
  -webkit-animation: animatedBackground 40s linear infinite;
          animation: animatedBackground 40s linear infinite;
}
.reflection-wrapper {
  max-height:50px;
  overflow: hidden;
}
.c2 {
  -webkit-transform: rotate3d(1, 0, 0, 0deg) rotate3d(0, 1, 0, 180deg) rotate3d(0, 0, 1, 180deg);
          transform: rotate3d(1, 0, 0, 0deg) rotate3d(0, 1, 0, 180deg) rotate3d(0, 0, 1, 180deg);
  -webkit-backface-visibility: visible;
          backface-visibility: visible;
  opacity: .4;
  background-color: gray;
  -webkit-filter: blur(1px);
          filter: blur(1px);
  z-index: 15;
  position: relative;
}
@-webkit-keyframes animatedBackground {
	from { background-position: -30% 0; }
	to { background-position: 130% 0; }
}
@keyframes animatedBackground {
	from { background-position: -30% 0; }
	to { background-position: 130% 0; }
}



